import React, { lazy, Suspense, useEffect, useState } from 'react';
import "./App.css";
import "../node_modules/bootstrap-dark-5/dist/css/bootstrap-dark.min.css"; //npm i bootstrap-dark-5 boostrap
import "../node_modules/bootstrap/dist/js/bootstrap.bundle";
import "../node_modules/bootstrap/dist/js/bootstrap.bundle.min.js";
import { ToastContainer } from "react-toastify";

import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
// import Navbar from './components/Navbar';

import { CartProvider } from "./components/ContextReducer";
import "react-toastify/dist/ReactToastify.css";
import Loader from './components/Loader.js';
import LoaderMain from './components/LoaderMain.js';

const Home = lazy(() => import('./screens/Home'));
const Signup = lazy(() => import('./screens/Signup'));
const Login = lazy(() => import('./screens/Login'));
const MyOrder = lazy(() => import('./screens/MyOrder'));
const Allorders = lazy(() => import('./screens/Allorders'));
const CompletedOrders = lazy(() => import('./screens/CompletedOrders'));
const AddFoodData = lazy(() => import('./screens/AddFoodData'));
const ProductListing = lazy(() => import('./screens/ProductListing.js'));

function App() {
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Simulating an asynchronous operation (e.g., fetching data)
    const fetchData = async () => {
      // Perform your asynchronous operations here
      // For example, you might fetch data from an API

      // Simulating a delay (remove this in your actual implementation)
      await new Promise(resolve => setTimeout(resolve, 1500));

      // After the data is fetched or any other asynchronous operation is done
      setLoading(false);
    };

    fetchData();
  }, []);

  if (loading) {
    return <LoaderMain />
  }
  return (
    <CartProvider>
      <Suspense fallback={<Loader />}>
        <Router>
          <div>
            <ToastContainer style={{ color: "red" }} />
            <Routes>
              <Route exact path="/" element={<Home />} />
              <Route exact path="/login" element={<Login />} />
              <Route exact path="/signup" element={<Signup />} />
              <Route exact path="/myorder" element={<MyOrder />} />
              <Route exact path="/allorders" element={<Allorders />} />
              <Route exact path="/completedorders" element={<CompletedOrders />} />
              <Route exact path="/addfooddata" element={<AddFoodData />} />
              <Route exact path="/productlisting" element={<ProductListing />} />
            </Routes>
          </div>
        </Router>
      </Suspense>
    </CartProvider>
  );
}

export default App;
